  "code": "import React from 'react';\r\nimport PropTypes from 'prop-types';\r\n\r\n/** Label with required field display, htmlFor, and block styling */\r\nfunction Label({htmlFor, label, required}) {\r\n  return (\r\n    <label style={{display: 'block'}} htmlFor={htmlFor}>\r\n      {label} { required && <span style={{color: 'red'}}> *</span> }\r\n    </label>\r\n  )\r\n}\r\n\r\nLabel.propTypes = {\r\n  /** HTML ID for associated input */\r\n  htmlFor: PropTypes.string.isRequired,\r\n\r\n  /** Label text */\r\n  label: PropTypes.string.isRequired,\r\n\r\n  /** Display asterisk after label if true */\r\n  required: PropTypes.bool\r\n};\r\n\r\nexport default Label;\r\n",
  "examples": [{
    "name": "ExampleOptional",
    "description": "",
    "code": "import React from 'react';\r\nimport Label from 'spunky/Label';\r\n\r\nexport default function ExampleOptional() {\r\n    return <Label htmlFor='test' label='test' />;\r\n}"
  }, {
    "name": "ExampleRequired",
    "description": "",
    "code": "import React from 'react';\r\nimport Label from 'spunky/Label';\r\n\r\nexport default function ExampleRequired() {\r\n    return <Label htmlFor='test' label='test' required />;\r\n}"
  }]
